<template>
  <Modal v-model="isShow" :title="title">
    <template #default>
      <IframeRender v-if="renderType === 'iframe'" :css="styles" style="width: 100%; height: 600px">
        <div class="html-template" v-html="sanitizedBody"></div>
      </IframeRender>
      <div v-else v-html="sanitizedBody" class="ck-content" />
    </template>
    <template #footer>
      <div class="text-left">
        <a
          href="#!"
          @click.prevent="handleClose"
          class="text-red-04 hover:text-red-02"
        >
          Cancel
        </a>
      </div>
    </template>
  </Modal>
</template>

<script>
import { computed, defineComponent, ref } from 'vue'

import { sanitize } from 'utils/dom'

import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import IframeRender from 'components/IframeRender.vue'

export default defineComponent({
  name: 'PreviewEmailTemplate',
  components: {
    Modal,
    Button,
    IframeRender
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    body: {
      type: String,
      default: ''
    },
    renderType: {
      type: String,
      default: "iframe" // "iframe | direct"
    },
		title: {
			type: String,
			default: 'Preview Email'
		}
  },
  emits: ['update:show'],
  setup(props, { emit }) {
    const styles = ref(
      `.html-template {  font-family: Arial, Helvetica, sans-serif; all: revert; }`
    )
    const isShow = computed({
      get() {
        return props.show
      },
      set(newVal) {
        emit('update:show', newVal)
      }
    })
    const handleClose = () => {
      isShow.value = false
    }
    const sanitizedBody = computed(() => sanitize(props.body))
    const renderType = computed(() => sanitize(props.renderType))
    return {
      styles,
      isShow,
      renderType,
      sanitizedBody,
      handleClose
    }
  }
})
</script>
